.login_content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
}

.img img {
  width: 500px;
}

.form {
  width: 360px;
}

.login_content img {
  height: 100px;
}

.login_content h2 {
  margin: 15px 0;
  color: #333;
  text-transform: uppercase;
  font-size: 2.9rem;
}

.login_content .input_div {
  position: relative;
  display: grid;
  grid-template-columns: 7% 93%;
  margin: 25px 0;
  padding: 5px 0;
  border-bottom: 2px solid #d9d9d9;
}

.login_content .input_div.one {
  margin-top: 0;
}

.i {
  color: #d9d9d9;
  display: flex;
  justify-content: center;
  align-items: center;
}

.i i {
  transition: 0.3s;
}

.input_div > div {
  position: relative;
  height: 45px;
}

.input_div > div > h5 {
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #999;
  font-size: 18px;
  transition: 0.3s;
}

.input_div ~ .before,
.input_div:after,
.input_div:before {
  content: '';
  position: absolute;
  bottom: -2px;
  width: 0%;
  height: 2px;
  background-color: #38d39f;
  transition: 0.4s;
}

.before {
  width: 20em;
  height: 20em;
}

.input_div ~ .before {
  right: 50%;
}

.input_div:before {
  right: 50%;
}

.input_div:after {
  left: 50%;
}

.input_div.focus ~ .before,
.input_div.focus:after,
.input_div.focus:before {
  width: 50%;
}

.input_div.focus > div > h5 {
  top: -5px;
  font-size: 15px;
}
.input_div.focus > .i {
  color: #38d39f;
}
.input_div.focus > .i > i {
  color: #38d39f;
}

.input_div > div > input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background: none;
  padding: 0.5rem 0.7rem;
  font-size: 1.2rem;
  color: #555;
  font-family: 'poppins', sans-serif;
}

.btn {
  display: block;
  width: 100%;
  height: 50px;
  text-align: center;
  border-radius: 25px;
  outline: none;
  border: none;
  background-image: linear-gradient(to right, #32be8f, #38d39f, #32be8f);
  background-size: 200%;
  font-size: 1.2rem;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  text-transform: uppercase;
  margin: 1rem 0;
  cursor: pointer;
  transition: 0.5s;
}
.btn:hover {
  background-position: right;
}
